import React from 'react';
import  './style.scss';
import { Carousel} from 'antd-mobile'
function HomeNews(){
    return (
        <div className='news'>
            <div className="title">
                <img src="data:image/png;base64," alt=""/>
            </div>
            <div className='newcar'>
                <Carousel className="my-carousel"
                    vertical
                    dots={false}
                    dragging={false}
                    swiping={false}
                    autoplay
                    infinite
                >
                    <div className="v-item">超买电脑版的网址是多少？</div>
                </Carousel>
            </div>
            
        </div>
    )
}

export default HomeNews;