import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import classNames from 'classnames';
import Topbar from '@/components/Topbar';
import style from './style.module.scss';
import { List, InputItem, WhiteSpace } from 'antd-mobile';
class Register extends Component {
    state = {
        tabs: [
            { title: '手机', num: 1 },
            { title: '邮箱', num: 2 }
        ],
        idx: 1
    }
    render() {
        return (
            <div className={style.contain}>
                <Topbar title='注册' handleBack={() => { this.props.history.goBack() }} right handleRight={() => { this.props.history.push('/login') }} />
                <div className={style.nav}>
                    {
                        this.state.tabs.map(item => {
                            return (
                                <div key={item.num}
                                    className={classNames({
                                        [style.item]: true,
                                        [style.borderBottom]: this.state.idx === item.num
                                    })}
                                    onClick={() => {
                                        this.setState({
                                            idx: item.num
                                        })
                                    }}
                                >
                                    {item.title}
                                </div>
                            )
                        })
                    }
                </div>
                <WhiteSpace size='lg' />
                <List>
                    {
                        this.state.idx === 1 ? (
                            <InputItem
                                labelNumber={2}
                                type='phone'
                                placeholder='请输入手机号'
                            ><img src="data:image/png;base64," alt="" /></InputItem>
                        ) : (
                                <InputItem
                                    labelNumber={2}
                                    type='phone'
                                    placeholder='请输入邮箱'
                                ><img src="data:image/png;base64," alt="" /></InputItem>
                            )
                    }
                    <InputItem
                        labelNumber={2}
                        type='phone'
                        placeholder='请设置6-20位登陆密码'
                    ><img src="data:image/png;base64," alt="" /></InputItem>
                    <InputItem
                        labelNumber={2}
                        type='phone'
                        placeholder='请再次输入密码'
                    ><img src="data:image/png;base64," alt="" /></InputItem>
                </List>
                <div className={style.Button}>
                    <button type="button">立即注册</button>
                </div>
            </div>
        )
    }
}

export default withRouter(Register);