import React, { Component } from 'react';
import { ActivityIndicator } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import './style.scss';
import axios from 'axios';
class HomeListContent extends Component{
    state={
        listData:[],
        loading:false
    }
    handleToDetail(id, sell_type, user_id){
        let {history}=this.props;
        history.push({ pathname: `/product/detail/${id}`, state: { sell_type, user_id}});
    }
    componentWillMount(){
        this.setState({
            loading:true
        })
        //通过父组件传回来的值进行判断
        let {cid} =this.props;
        let recommend=cid ? '1' : '4';

        //生成formData数据结构进行请求
        let formData = new FormData();
        formData.append("page", "1");
        formData.append("recommend", recommend); 
        formData.append("cid", cid); 
        formData.append("device", 'mobile'); 

        //发送请求
        axios.post('api/v1/basic/product/Lists', formData)
        .then((res)=>{
            let {data} =res.data;
            this.setState({
                listData:data.data,
                loading:false
            })
        }).catch((err)=>{
            console.log(err)
        })
    }
    render(){
        let { listData,loading } =this.state;
        return (
            <div className='listcontent'>
                <ActivityIndicator toast text="Loading" animating={loading}/>
                <div className="section-title">
                    <span className="divider">
                        <span className="new-color">
                            <img src="data:image/png;base64," alt=""/>
                            超买新品
                        </span>
                    </span>
                </div>
                <div className="list-wrapper">
                    <div className="list-content">
                    {
                        listData.map(item=>(
                            <div className="list-item" key={item.product_name} onClick={this.handleToDetail.bind(this, item.product_id, item.sell_type, item.user_id)}>
                                <div className="square-box">
                                    <img src={item.image_thumb} alt="" />
                                </div>
                                <div className='detail-content section-content'>
                                    <div className='row-mb12'>{item.product_name}</div>
                                    <div className='text-small'>
                                        <div className='price'>
                                            <span>
                                                <span className='theme-color'>
                                                    <span className='text-small'>￥</span>
                                                    <span className='text-big'>{item.sell_price}</span>
                                                </span>
                                                <del className='tip'>
                                                    <span className='text-small'>￥</span>
                                                    {item.score}
                                            </del>
                                            </span>
                                        </div>
                                        <div className='flex-between'>
                                            <span>已售{item.sell_num}件</span><span>进入店铺</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))
                    }
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(HomeListContent);